.task>.project {
    margin-top: 2rem;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.project>.sub-title {
    align-self: flex-start;
    font-weight: bold;
    font-size: 1.4rem;
    margin-top: 2rem;
    margin-bottom: 1rem;
}

.task {
    display: grid;
    grid-template-columns: auto 1fr;
    grid-template-rows: auto auto;
    align-items: center;
    gap: 0.5rem;
    padding: 1rem;

    border-top: 1px solid rgba(0, 0, 0, 0.2);
    /* border-bottom: 1px solid rgba(0, 0, 0, 0.3); */
}

.task>input[type="checkbox"] {
    appearance: none;
    border: 1px solid #6c6a68;
    border: 50%;
    width: 1.5rem;
    aspect-ratio: 1/1;
    border-radius: 50%;
    position: relative;
}

.task>input[type="checkbox"]:checked {
    background: rgba(0, 0, 0, 0.2);
    padding: 0.5rem;

}


.task>.time {
    display: flex;
    align-items: center;
    grid-column: 2;
    color: green;

    position: static;
}

.time>svg {
    width: 1rem;
}

.task>.text {
    font-size: 1.2rem;
    font-weight: 300;
}

.time>svg+svg {
    margin-left: 0.6rem;
}

.task>.plus {
    color: #dc4a44;
    font-size: 2.5rem;
    font-weight: 100;
    margin-left: 0.3rem;
}

.task>.add-text {
    font-size: 1.2rem;
    margin-left: 0.3rem;
    color: rgba(0, 0, 0, 0.5);
    border: none;
}

.task>.add-text:focus {
    color: rgba(0, 0, 0, 1);
}`, "",{"version":3,"sources":["webpack://./src/css/today.css"],"names":[],"mappings":"AAAA,cAAc;AACd;IACI,gBAAgB;IAChB,aAAa;IACb,sBAAsB;IACtB,mBAAmB;AACvB;;AAEA;IACI,sBAAsB;IACtB,iBAAiB;IACjB,iBAAiB;IACjB,gBAAgB;IAChB,mBAAmB;AACvB;;AAEA;IACI,aAAa;IACb,+BAA+B;IAC/B,6BAA6B;IAC7B,mBAAmB;IACnB,WAAW;IACX,aAAa;;IAEb,wCAAwC;IACxC,iDAAiD;AACrD;;AAEA;IACI,gBAAgB;IAChB,yBAAyB;IACzB,WAAW;IACX,aAAa;IACb,iBAAiB;IACjB,kBAAkB;IAClB,kBAAkB;AACtB;;AAEA;IACI,8BAA8B;IAC9B,eAAe;;AAEnB;;;AAGA;IACI,aAAa;IACb,mBAAmB;IACnB,cAAc;IACd,YAAY;;IAEZ,gBAAgB;AACpB;;AAEA;IACI,WAAW;AACf;;AAEA;IACI,iBAAiB;IACjB,gBAAgB;AACpB;;AAEA;IACI,mBAAmB;AACvB;;AAEA;IACI,cAAc;IACd,iBAAiB;IACjB,gBAAgB;IAChB,mBAAmB;AACvB;;AAEA;IACI,iBAAiB;IACjB,mBAAmB;IACnB,yBAAyB;IACzB,YAAY;AAChB;;AAEA;IACI,uBAAuB;AAC3B","sourcesContent":["/* today.css */\n.task>.project {\n    margin-top: 2rem;\n    display: flex;\n    flex-direction: column;\n    align-items: center;\n}\n\n.project>.sub-title {\n    align-self: flex-start;\n    font-weight: bold;\n    font-size: 1.4rem;\n    margin-top: 2rem;\n    margin-bottom: 1rem;\n}\n\n.task {\n    display: grid;\n    grid-template-columns: auto 1fr;\n    grid-template-rows: auto auto;\n    align-items: center;\n    gap: 0.5rem;\n    padding: 1rem;\n\n    border-top: 1px solid rgba(0, 0, 0, 0.2);\n    /* border-bottom: 1px solid rgba(0, 0, 0, 0.3); */\n}\n\n.task>input[type=\"checkbox\"] {\n    appearance: none;\n    border: 1px solid #6c6a68;\n    border: 50%;\n    width: 1.5rem;\n    aspect-ratio: 1/1;\n    border-radius: 50%;\n    position: relative;\n}\n\n.task>input[type=\"checkbox\"]:checked {\n    background: rgba(0, 0, 0, 0.2);\n    padding: 0.5rem;\n\n}\n\n\n.task>.time {\n    display: flex;\n    align-items: center;\n    grid-column: 2;\n    color: green;\n\n    position: static;\n}\n\n.time>svg {\n    width: 1rem;\n}\n\n.task>.text {\n    font-size: 1.2rem;\n    font-weight: 300;\n}\n\n.time>svg+svg {\n    margin-left: 0.6rem;\n}\n\n.task>.plus {\n    color: #dc4a44;\n    font-s