.task-pane>form {
    margin-top: 1.5rem;
    width: 100%;
    border: 1px solid rgba(0, 0, 0, 0.5);
    border-radius: 0.5rem;
    padding: 1rem 0 0 0;

    display: grid;
    grid-template-rows: auto auto auto auto;
    gap: 0.1rem;
    ;
}

.task-pane>form>input {
    border: none;
    padding: 0.3rem;
    font-size: 1.2rem;
    padding-left: 1rem;
}

.task-pane>form>input:focus {
    outline: none;
    box-shadow: none;
}

.task-pane>form>input[name="task_title"] {
    font-weight: bold;
    font-size: 1.5rem;
}

.buttons {
    margin-top: 1.3rem;
    display: flex;
    padding-left: 1rem;
    margin-bottom: 1rem;
}

.buttons>button {
    border: 1px solid rgba(0, 0, 0, 0.3);
    background-color: transparent;
    border-radius: .25rem;

    display: flex;
    align-items: center;
    justify-content: center;
    padding: 0.5rem;
    font-size: 1.2rem;
    color: rgba(0, 0, 0, 0.3);
    font-weight: 600;
    margin-right: 0.8rem;
}

.buttons>button:hover {
    color: rgba(0, 0, 0, 0.5);
}

.buttons>#task-more>svg {
    margin-right: 0;
}

.buttons>button>svg {
    width: 1.3rem;
    margin-right: 0.5rem;
}

.bottom-form {
    border-top: 1px solid rgba(0, 0, 0, 0.3);
}

.bottom-form {
    /* padding: 1rem; */
    display: flex;
    align-items: center;
    padding: 1rem;
}

.bottom-form>svg {
    width: 1.2rem;
    color: rgba(0, 0, 0, 0.5);
    margin-right: 0.5rem;
}

.bottom-form>select {
    border: none;
    background-color: transparent;
    font-size: 1.2rem;
    color: rgba(0, 0, 0, 0.5);
    font-weight: bold;
    width: 20%;
}

.bottom-form>select:focus {
    appearance: none;
    border: none;
    -webkit-appearance: none;
    /* Safari/Chrome */
    -moz-appearance: none;
    /* Firefox */
    border: none;
    outline: none;
    /* Removes default focus outline */

}

.bottom-form>button {
    border: none;
    padding: 0.6rem 1rem;
    border-radius: 0.3rem;
    margin-right: 0.6rem;
    font-size: 1.3rem;
    background-color: rgba(0, 0, 0, 0.1);
    /* position: relative; */
    margin-right: 0.8rem;
    /* margin-top: 1rem; */
}

button#task-cancel {
    margin-left: auto;
}

button#task-add {
    color: white;
    background-color: #dc4a44;
    margin-right: 1rem;
}`, "",{"version":3,"sources":["webpack://./src/css/project.css"],"names":[],"mappings":"AAAA,gBAAgB;;AAEhB;IACI,kBAAkB;IAClB,WAAW;IACX,oCAAoC;IACpC,qBAAqB;IACrB,mBAAmB;;IAEnB,aAAa;IACb,uCAAuC;IACvC,WAAW;;AAEf;;AAEA;IACI,YAAY;IACZ,eAAe;IACf,iBAAiB;IACjB,kBAAkB;AACtB;;AAEA;IACI,aAAa;IACb,gBAAgB;AACpB;;AAEA;IACI,iBAAiB;IACjB,iBAAiB;AACrB;;AAEA;IACI,kBAAkB;IAClB,aAAa;IACb,kBAAkB;IAClB,mBAAmB;AACvB;;AAEA;IACI,oCAAoC;IACpC,6BAA6B;IAC7B,qBAAqB;;IAErB,aAAa;IACb,mBAAmB;IACnB,uBAAuB;IACvB,eAAe;IACf,iBAAiB;IACjB,yBAAyB;IACzB,gBAAgB;IAChB,oBAAoB;AACxB;;AAEA;IACI,yBAAyB;AAC7B;;AAEA;IACI,eAAe;AACnB;;AAEA;IACI,aAAa;IACb,oBAAoB;AACxB;;AAEA;IACI,wCAAwC;AAC5C;;AAEA;IACI,mBAAmB;IACnB,aAAa;IACb,mBAAmB;IACnB,aAAa;AACjB;;AAEA;IACI,aAAa;IACb,yBAAyB;IACzB,oBAAoB;AACxB;;AAEA;IACI,YAAY;IACZ,6BAA6B;IAC7B,iBAAiB;IACjB,yBAAyB;IACzB,iBAAiB;IACjB,UAAU;AACd;;AAEA;IACI,gBAAgB;IAChB,YAAY;IACZ,wBAAwB;IACxB,kBAAkB;IAClB,qBAAqB;IACrB,YAAY;IACZ,YAAY;IACZ,aAAa;IACb,kCAAkC;;AAEtC;;AAEA;IACI,YAAY;IACZ,oBAAoB;IACpB,qBAAqB;IACrB,oBAAoB;IACpB,iBAAiB;IACjB,oCAAoC;IACpC,wBAAwB;IACxB,oBAAoB;IACpB,sBAAsB;AAC1B;;AAEA;IACI,iBAAiB;AACrB;;AAEA;IACI,YAAY;IACZ,yBAAyB;IACzB,kBAAkB;AACtB","sourcesContent":["/* project.css */\n\n.task-pane>form {\n    margin-top: 1.5rem;\n    width: 100%;\n    border: 1px solid rgba(0, 0, 0, 0.5);\n    border-radius: 0.5rem;\n    padding: 1rem 0 0 0;\n\n    display: grid;\n    grid-template-rows: auto auto auto auto;\n    gap: 0.1rem;\n    ;\n}\n\n.task-pane>form>input {\n    border: none;\n    padding: 0.3rem;\n    font-size: 1.2rem;\n    padding-left: 1rem;\n}\n\n.task-pane>form>input:focus {\n    outline: none;\n    box-shadow: none;\n}\n\n.task-pane>form>input[name=\"task_title\"] {\n    font-weight: bold;\n    font-size: 1.5rem;\n}\n\n.buttons {\n    margin-top: 1.3rem;\n    display: flex;\n    padding-left: 1rem;\n    margin-bottom: 1rem;\n}\n\n.buttons>button {\n    border: 1px solid rgba(0, 0, 0, 0.3);\n    background-color: transparent;\n    border-radius: .25rem;\n\n    display